import React, { useState, useEffect } from "react";
import { Spinner } from "@fluentui/react-components";
import {
  Button,
  Dropdown,
  Option,
  Label,
  Toaster,
  Toast,
  ToastTitle,
  useToastController,
} from "@fluentui/react-components";
import { ArrowLeft20Filled } from "@fluentui/react-icons";
import { createClient } from "@supabase/supabase-js";

// Supabase credentials
const SUPABASE_URL = "https://dqfdkblzexlyikggnzgf.supabase.co";
const SUPABASE_KEY = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImRxZmRrYmx6ZXhseWlrZ2duemdmIiwicm9sZSI6InNlcnZpY2Vfcm9sZSIsImlhdCI6MTc1NzExMjc5NiwiZXhwIjoyMDcyNjg4Nzk2fQ.yKKxTXYf-WXIrFeunLRRiEC3my74ffifm9pWwobW7xc";

const supabase = createClient(SUPABASE_URL, SUPABASE_KEY);

const dbColumns = [
  "Scenario",
  "Period",
  "Entity",
  "Account",
  "Department",
  "Currency",
  "Subline",
  "Value",
];

const excelGreen = "#217346";
const excelGreenHover = "#145c32";

const Home: React.FC = () => {
  const [headers, setHeaders] = useState<string[]>([]);
  const [rows, setRows] = useState<(string | number)[][]>([]);
  const [mapping, setMapping] = useState<Record<string, string>>({});
  const [loading, setLoading] = useState(false);
  const [step, setStep] = useState<"init" | "dropdowns">("init");

  const { dispatchToast } = useToastController();

  const notify = (msg: string, intent: "success" | "error" = "success") => {
    dispatchToast(
      <Toast>
        <ToastTitle>{msg}</ToastTitle>
      </Toast>,
      { intent }
    );
  };

  const getSelectedColumns = async () => {
    try {
      await Excel.run(async (context) => {
        const range = context.workbook.getSelectedRange();
        range.load("values");
        await context.sync();

        const values = range.values as (string | number)[][];
        // Check if the selection is empty or contains no valid data
        if (!values || values.length === 0 || values[0].every((v) => v === "")) {
          notify("No data selected in Excel", "error");
          return;
        }

        // Ensure the selection has headers and at least one data row
        if (values.length < 2) {
          notify("Please select a range with headers and at least one data row", "error");
          return;
        }

        const excelHeaders = values[0].map((h) => String(h).trim());
        // Check if headers are valid (not all empty)
        if (excelHeaders.every((h) => h === "")) {
          notify("Selected range has no valid headers", "error");
          return;
        }

        const excelRows = values.slice(1).filter((row) => row.some((cell) => String(cell).trim() !== ""));
        // Check if there are any valid data rows
        if (excelRows.length === 0) {
          notify("Selected range has no valid data rows", "error");
          return;
        }

        setHeaders(excelHeaders);
        setRows(excelRows);
        setStep("dropdowns");
        notify("Headers loaded from Excel!");
      });
    } catch (err) {
      console.error("Error getting Excel data:", err);
      notify("Failed to load Excel data", "error");
    }
  };

  const handleSelect = (dbCol: string, excelHeader: string) => {
    setMapping((prev) => ({ ...prev, [dbCol]: excelHeader }));
  };

  const saveToSupabase = async () => {
    setLoading(true);
    try {
      if (rows.length === 0 || headers.length === 0) {
        notify("No data to upload", "error");
        return;
      }

      // Build payloads row by row
      const payloads = rows.map((row) => {
        const record: Record<string, any> = {};
        dbColumns.forEach((dbCol) => {
          const excelHeader = mapping[dbCol];
          if (excelHeader) {
            const colIndex = headers.indexOf(excelHeader);
            if (colIndex !== -1) {
              record[dbCol] = row[colIndex];
            }
          }
        });
        return record;
      });

      // Insert all rows into Supabase
      const { error } = await supabase.from("planxls_data").insert(payloads);
      if (error) throw error;

      notify("✅ Data uploaded to Supabase!");
    } catch (err) {
      console.error("Save failed:", err);
      notify("Save failed", "error");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    const handleResizeObserverError = (e: ErrorEvent) => {
      if (
        e.message.includes(
          "ResizeObserver loop completed with undelivered notifications"
        )
      ) {
        e.preventDefault();
      }
    };
    window.addEventListener("error", handleResizeObserverError);
    return () =>
      window.removeEventListener("error", handleResizeObserverError);
  }, []);

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: step === "init" ? "center" : "flex-start",
        alignItems: "center",
        background: "#ffffff",
        padding: "20px",
        position: "relative",
      }}
    >
      {step === "dropdowns" && (
        <Button
          icon={<ArrowLeft20Filled />}
          appearance="subtle"
          onClick={() => setStep("init")}
          style={{
            position: "absolute",
            top: "16px",
            left: "16px",
            color: excelGreen,
          }}
          onMouseEnter={(e) =>
            ((e.currentTarget as HTMLButtonElement).style.color = excelGreenHover)
          }
          onMouseLeave={(e) =>
            ((e.currentTarget as HTMLButtonElement).style.color = excelGreen)
          }
        />
      )}

      <h2
        style={{
          textAlign: "center",
          fontSize: "24px",
          fontWeight: 600,
          color: excelGreen,
          margin: step === "init" ? "0 0 20px 0" : "40px 0 20px 0",
        }}
      >
        Excel ↔ Supabase Sync
      </h2>

      {step === "init" && (
        <Button
          appearance="primary"
          style={{
            backgroundColor: excelGreen,
            color: "white",
            width: "250px",
            fontSize: "16px",
            padding: "12px",
            marginBottom: "35px",
          }}
          onClick={getSelectedColumns}
        >
          Load Excel Data
        </Button>
      )}

      {step === "dropdowns" && (
        <>
          {dbColumns.map((dbCol, index) => (
            <div
              key={dbCol}
              style={{
                display: "flex",
                flexDirection: "column",
                width: "100%",
                maxWidth: "600px",
                marginBottom: "16px",
                marginTop: index === 0 ? "18px" : "0",
              }}
            >
              <Label
                style={{
                  fontSize: "14px",
                  fontWeight: 500,
                  color: "#444",
                  marginBottom: "4px",
                }}
              >
                {dbCol}
              </Label>
              <Dropdown
                placeholder={`Map to Excel header`}
                onOptionSelect={(_, data) =>
                  handleSelect(dbCol, data.optionValue as string)
                }
              >
                {headers.map((h) => (
                  <Option key={h} value={h}>
                    {h}
                  </Option>
                ))}
              </Dropdown>
            </div>
          ))}

          <Button
            appearance="primary"
            onClick={saveToSupabase}
            disabled={loading || Object.keys(mapping).length === 0}
            style={{
              backgroundColor: excelGreen,
              color: "white",
              padding: "12px 24px",
              fontSize: "16px",
              marginTop: "20px",
            }}
          >
            {loading ? "Saving..." : "🚀 Save to Supabase"}
          </Button>
        </>
      )}

      {loading && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(255, 255, 255, 0.7)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 9999,
          }}
        >
          <Spinner size="extra-large" />
        </div>
      )}

      <Toaster />
    </div>
  );
};

export default Home;